<template>
  <div class="home">
    <van-checkbox-group v-model="result">


      <van-row>
        <van-col span="24" class="box" v-for="(item,index) in prosData" :key="index">

          <van-row type="flex" justify="center">

            <van-col span="5">
              <van-checkbox :name="index" @click="dx(index)"></van-checkbox>
            </van-col>

            <van-col span="24">

              <van-col span="6" class="zp">
                <img class="auto-img" :src="item.url" alt="">
              </van-col>
              <van-col span="15" class="jg">
                <van-col span="24" class="jz zt">
                  {{item.name}}
                </van-col>

                <van-col span="9" class="fl">
                  价格：{{item.price}}
                </van-col>

                <van-col span="9" class="fl">
                  数量：33
                </van-col>

              </van-col>
            </van-col>

          </van-row>

        </van-col>


      </van-row>

      <van-submit-bar  :price="zjg"  button-text="提交订单">
        <van-checkbox @click="qx">全选</van-checkbox>
        <span slot="tip">
          你的收货地址不支持同城送, <span>修改地址</span>
        </span>
      </van-submit-bar>

    </van-checkbox-group>



  </div>
</template>

<style lang="less">
  .box {
    height: 2rem;
    background-color: #f5f5f5;
    padding: .5rem .1rem;

  }

  .zp {
    margin-right: .5rem;
  }

  .zt {
    font-size: .3rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .jz {
    text-align: center;
  }

  .fl {
    margin-left: .5rem;
    color: #888;
  }
</style>

<script>
  // @ is an alias to /src

  export default {
    name: 'home',

    data() {
      return {
        result: [],
        prosData: null,
        isqx: true,
        zjg: 0
      };
    },
    created() {
      let self = this;
      this.ajax({
        type: 'GET',
        url: 'http://api.kangliuyong.com:9002/pros',
        isAsync: true,
        success: function(data) {
          for (let i = 0; i < data.length; i++) {
            data[i].isLike = false;
          }

          self.prosData = data.concat();

        }
      })
    },


    methods: {

      dx(index) {
        this.result.slice(index, 1);





      },


      qx() {
        if (this.isqx) {
          for (let i = 0; i < this.prosData.length; i++) {
            this.result.push(i);
            this.isqx = false;

          }

          for (let i = 0; i < this.result.length; i++) {
            this.zjg += this.prosData[this.result[i]].price;

          }
          
        } else {
          this.zjg=0;
          this.result = [];
          this.isqx = true;

        }
      },

      ajax(o) {

        let xhr = new XMLHttpRequest();


        xhr.onreadystatechange = function() {

          if (this.readyState == 4 && this.status == 200) {

            o.success(this.response);
          }

        }

        xhr.open(o.type, o.url, o.isAsync);


        xhr.responseType = 'json';

        xhr.send();
      }



    }
  }
</script>
