import Vue from 'vue'
import App from './App.vue'
import router from './router'


import{
 Row,
 Col,
 Checkbox,
 CheckboxGroup,
 SubmitBar,
 Button,
 } from 'vant'

//根据屏幕适配rem
import 'lib-flexible/flexible'


Vue
.use(Row)
.use(Col)
.use(Checkbox)
.use(CheckboxGroup)
.use(SubmitBar)
.use(Button )


Vue.config.productionTip = false

new Vue({
  router,
  render: h => h(App)
}).$mount('#app')
